/* General header styling */

header {
    background-color: #333; /* Dark background */
    color: white;
    text-align: center;
    display: flex;
    justify-content: space-between; /* Creates space between h1 and nav */
    align-items: center; /* Vertically centers the content */
    padding: 0 30px;
    position: sticky;
    top: 0; /* Stick to the top of the page */
    z-index: 1000; /* Ensure the header stays on top of other content */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Slight shadow for distinction */
}

/* Styling for the h1 */
header h1 {
    font-size: 2.5em; /* Larger size for emphasis */
    font-weight: bold;
    margin: 0;
    color: #ff9800; /* Different color for distinction */
}

/* Styling for the navigation bar */
nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

nav ul li {
    display: inline-block;
    margin-right: 20px; /* Spacing between links */
}

nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 1.2em;
    font-weight: 600;
    transition: color 0.3s ease;
}

nav ul li a:hover {
    color: #ff9800; /* Highlight color on hover */
    text-decoration: underline;
}
